@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    font-family: 'Poppins', sans-serif;
}
.header{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 150px;   
}

.header1{
    font-size: .8em;
}

.header2{
    background-image: linear-gradient(rgba(0, 0, 20, 0.7), rgba(0, 0, 20, 0.7)), url(https://images.unsplash.com/photo-1487017159836-4e23ece2e4cf?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=751&q=80);
    background-size: 100% 130%;
    height: 800px;
    width: 100%;
}
.Margin{
    margin-top: 150px;
}
.button{
    font-size: 1.2em;
    opacity: .7;
    background-color: rgb(0,128,128);
    color: white;
   border-radius: 50px;
}
.button:hover{
    opacity: 1;
    text-transform: uppercase;
}

.h1{
    text-align: center;
    font-size: 3em;
    color:  white;
    font-style: normal;
    font-weight: normal;
    font-weight: 600;
}
.p{
    text-align: center;
    color:  white;
    font-size: 1.7em; 
    margin-top: -20px;
    margin-bottom: 150px;
}

a{
    opacity: .8;
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    padding: 15px 20px;
    position: relative;
    font-size: .9em;
}
a:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 5px;
    left: 50%;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
    -webkit-transform: scale(.7);
            transform: scale(.7);
}
.ahvr:hover{
    opacity: 1;
      
}
.ahvr:hover:after { 
    width: 100%; 
    left: 0;
}

  .profilepic:hover{
      -webkit-transform: scale(1.02);
              transform: scale(1.02);
  }

.aboutme{
 
    text-align: justify;
}
.text1{
    color: black;
    font-size: 2em;
