}

a{
    opacity: .8;
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    padding: 15px 20px;
    position: relative;
    font-size: .9em;
}
a:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 5px;
    left: 50%;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
    transform: scale(.7);
}
.ahvr:hover{
    opacity: 1;
      
}
.ahvr:hover:after { 
    width: 100%; 
    left: 0;
}

  .profilepic:hover{
      transform: scale(1.02);
  }

